import React, {useReducer, createContext} from 'react'
import { caseReducer } from '../reducer/caseReducer'

export const CaseController = createContext()

export default function CaseContext(props) {
    const [caseData, caseDataDispatch] = useReducer(caseReducer, [
        {
            id: "1",
            caseTitle: "",
            date: new Date(),
            place: "",
            related: 100,
            province: "",
            district: "",
            commune: "",
            village: "",
            remark: "",
            lat:12.23434,
            long:34.3344,
        },
        {
            id: "2",
            caseTitle: "",
            date: new Date(),
            place: "",
            related: 100,
            province: "",
            district: "",
            commune: "",
            village: "",
            remark: "",
            lat:12.23434,
            long:34.3344,
        },
    ])

    const [subCaseData, subCaseDataDispatch] = useReducer(caseReducer, [
        {
            id: "1",
            caseId: "1",
            name: "",
            gender: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            status: "",
            relatedInfo: ""
        },
        {
            id: "2",
            caseId: "2",
            name: "",
            gender: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            status: "",
            relatedInfo: ""
        },
    ])

    return (
        <CaseController.Provider
            value={{
                caseData,
                subCaseData,

                caseDataDispatch,
                subCaseDataDispatch,
            }}
        >
            {props.children}
        </CaseController.Provider>
    )
}
