import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const testCol = ({handleSampleTestDelete}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 30,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
            width: 80,
            render: (text, record) => (
                <Space size="middle">
                    {moment(record.date).format("DD MM YYYY")}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'times',
            key: 'times',
            width: 40,
        },
        {
            title: '',
            dataIndex: 'location',
            key: 'location',
            width: 50,
        },
        {
            title: '',
            dataIndex: 'sampleTest',
            key: 'sampleTest',
            width: 60,
            render:(text,record)=>(
                <Space size="middle">
                    { record?.result ? "":"" }
                    {/* {console.log(record?.result )} */}
                </Space>
            )
        },
        {
            title: '',
            dataIndex: 'symptom',
            key: 'symptom',
            width: 50,
        },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 100,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleSampleTestDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]

    return array
}