import React, { useContext, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, DatePicker,message } from 'antd'
import { provinceData, districtData, communeData, villageData, genderData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage } from '../../../function/fn'
import { PeopleController } from '../../../context/peopleContext'
import { useMutation,useQuery } from '@apollo/client';
import { RECORD_SAMPLETEST } from '../../../graphql/people';
import moment from 'moment'

const {Option} = Select

export default function AddPeopleTest({ open, setOpen, peopleID, setRefetch }) {
    //const { peopleDataDispatch } = useContext(PeopleController)

    let [form] = Form.useForm()

    const [recordSampleTest,{loading}] = useMutation(RECORD_SAMPLETEST,{
        onCompleted:()=>{
            message.success("")
        }
    })

    // console.log(peopleID)

    const onFinish = (values) => {
        console.log('Success:', values);

        //peopleDataDispatch({ type: 'ADD_PEOPLE', payload: values })

        recordSampleTest({
            variables:{
                date:moment(values.date).format(),
                times: parseInt(values.times),
                location:values.location,
                result:values.result,
                symptom:values.symptom,
                other:values.other,
                personalInfoId:peopleID,
                labFormCompletedBy: values.labFormCompletedBy,
                laboratory:values.laboratory,
                reasonForTesting:values.reasonForTesting,
                symptomStart:moment(values.symptomStart).format(),
            }
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addPeopleTest"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="date"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{width:"100%"}}/>
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="location"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="reasonForTesting"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:2 }}>
                        <Form.Item
                            name="symptomStart"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{width:"100%"}}/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="specimentType"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset:2 }}>
                        <Form.Item
                            name="laboratory"
                            rules={[{ required: true, message: '!' }]}
                        >
                             <Select placeholder="" style={{ width: "100%" }}>
                                <Option value="IPC">IPC</Option>
                                <Option value="NIPH">NIPH</Option>
                                <Option value="PKML">PKML</Option>
                                <Option value="MKB">MKB</Option>
                                <Option value="BTB">BTB</Option>
                                <Option value="TKV">TKV</Option>
                                <Option value="SRH">SRH</Option>
                                <Option value="NBTC">NBTC</Option>
                                <Option value="SHV">SHV</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="result"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24}}>
                        <Form.Item
                            name="symptom"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>


                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"
                        
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11}}>
                        <Form.Item
                            name="labFormCompletedBy"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
