import React, {useContext, useEffect,useState} from 'react'
import { Modal, Form, Input, DatePicker, Row, Col, Button, message } from 'antd'
import {setEditCase} from '../../../function/set'
import { provinceData, districtData, communeData, villageData, genderData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage } from '../../../function/fn'
import { useMutation } from '@apollo/client'
import { UPDATE_CASE_BY_ID } from '../../../graphql/case'

export default function EditCase({ open, setOpen, data, caseId,setRetetch}) {

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")

    let [form] = Form.useForm()

    const [updateCase,{loading}] = useMutation(UPDATE_CASE_BY_ID,{
        onCompleted:()=>{
            setRetetch()
            message.success("")
        }
    })

    useEffect(() => {
        if (data !== undefined){
            form.setFieldsValue(setEditCase(data))
            setProvince(data.province)
            setDistrict(data.district)
            setCommune(data.commune)
        }

    }, [data])

    const onFinish = (values) => {

        updateCase({
            variables:{
                caseName:values.caseName,
                village: values.village === undefined ? "" : values.village,
                commune: values.commune === undefined ? "" : values.commune,
                district: values.district === undefined ? "" : values.district,
                province: values.province === undefined ? "" : values.province,
                date:values.date,
                long:values.long,
                lat:values.lat,
                other:values.other,
                id:caseId
            }
        })

        // caseDataDispatch({type: 'EDIT_CASE', payload: {...values, id: data.id}})
        // setData({...values, id: data.id})

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
            destroyOnClose={false} 
            getContainer={false}
            forceRender
        >
            <Form
                form={form}
                name="editCase"
                initialValues={setEditCase(data)}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{span:11}}>
                        <Form.Item
                            name="caseName"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{span:11, offset: 2}}>
                        <Form.Item
                            name="date"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: 'Please input your username!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: 'Please input your username!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: 'Please input your username!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    <Col xs={24}>
                        <Form.Item
                            name="other"
                            
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="long"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input type={1} placeholder="longtitude" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 , offset:2}}>
                        <Form.Item
                            name="lat"
                            rules={[{ required: true, message: 'Please input your username!' }]}
                        >
                            <Input type={1} placeholder="latitude" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button 
                            htmlType="submit"
                            type="primary"
                            style={{width: "100%"}}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
