import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

export const subCaseCol = ({handleDelete, handleEditSubCase}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 50,
            render: (text, record) => (
                <span size="middle">
                    {
                        record.lastName+" "+record.firstName
                    }
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 20,
            // render: (text, record) => (
            //     <Space size="middle">
            //         {moment(record.date).format("DD MM YYYY")}
            //     </Space>
            // ),
        },

        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                    {record.village + "," + record.commune + "," + record.district + "," + record.province}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 50,
            render: (text, record) => (
                <span size="middle">
                    {
                        // console.log(record.currentState.confirm)
                        record.currentState.confirm ? "" : ""
                    }
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'direct',
            key: 'direct',
            width: 50,
            render: (text, record) => (
                <span size="middle">
                    {
                        // console.log(record.currentState.confirm)
                        record ? "" : ""
                    }
                </span>
            ),
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 20,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {/* <span className="link" onClick={() => handleEditSubCase(record)}><EditOutlined /></span> */}
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => {handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}