import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

export const relatedCol = ({handleRelatedDelete}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <span size="middle">
                    {i += 1}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 80,
            render: (text, record) => (
                <span size="middle">
                    {record?.lastName+" " +record?.firstName}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 30,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width:100,
            render: (text, record) => (
            <span>
                    {(record.village !== "") && record.village+", "}
                    {record.commune !== "" && record.commune+", "}
                    {record.district !== "" && record.district+", "}
                    {record.province && record.province}
                </span>
            )
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 50,
            render: (text, record) => (
                <Space size="middle">
                    {record?.currentState.confirm ? "" : ""}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'direct',
            key: 'direct',
            width:50,
            render: (text, record) => (
                <Space size="middle">
                    {record?.direct? "" : ""}
                </Space>
            ),
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 20,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleRelatedDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]

    return array
}