import { Space, Tooltip, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import moment from 'moment'
import { Link } from 'react-router-dom'

export const caseCol = ({ handleDelete, limit, page }) => {
    let no = ((page - 1) * limit)
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                {console.log(page)}
                    {no += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'caseName',
            key: 'caseName',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'date',
            key: 'date',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                    {/* {record.date} */}
                    {moment(record.date).format("DD MM YYYY")}
                </Space>
            ),
        },

        {
            title: '',
            dataIndex: 'related',
            key: 'related',
            width: 100,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 150,
            render: (text, record) => (
                <Space size="middle">
                    {(record.village !== "") && record.village}
                    {record.commune !== "" && record.commune}
                    {record.district !== "" && record.district}
                    {record.province && record.province}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 150,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 50,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subCase/" + record.id}><EditOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}