import React, {useReducer, createContext} from 'react'
import { peopleReducer } from '../reducer/peopleReducer'

export const PeopleController = createContext()

export default function PeopleContext(props) {
    const [peopleData, peopleDataDispatch] = useReducer(peopleReducer, [
        {
            id: "1",
            idCard: "123123123",
            name: " ",
            gender: "",
            age: 25,
            nationality: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            job: "",
            tel: 10503375,
            remark: "",
        },
        {
            id: "2",
            idCard: "123123123",
            name: " 1",
            gender: "",
            age: 25,
            nationality: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            job: "",
            tel: 10503375,
            remark: "",
        },
    ])

    return (
        <PeopleController.Provider
            value={{
                peopleData,

                peopleDataDispatch,
            }}
        >
            {props.children}
        </PeopleController.Provider>
    )
}
