import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

export const peopleCol = ({handleDelete,limit,page}) => {
    let no = ((page-1) * limit)
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 60,
            render: (text, record, i) => (
                <Space size="middle">
                    {no+= 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 200,
            render: (text, record) => (
                <span size="middle">
                    {
                        record.lastName+" "+record.firstName
                    }
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'nationality',
            key: 'nationality',
            width: 70,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 250,
            render: (text, record) => (
                <span>
                    {(record.village !== "") && record.village+", "}
                    {record.commune !== "" && record.commune+", "}
                    {record.district !== "" && record.district+", "}
                    {record.province && record.province}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'occupation',
            key: 'occupation',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'interview',
            key: 'interview',
            width: 150,
            render: (text, record) => (
                <span>{record?.interviewed ? "" : ""}</span>
                )
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 80,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}