import { Space, Tooltip, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import moment from 'moment'
import { Link } from 'react-router-dom'

export const locationCol = ({ handleDelete, handleEdit, limit, page }) => {
    let no = ((page - 1) * limit)
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 50,
            render: (text, record, i) => (
                <Space size="middle">
                {console.log(page)}
                    {no += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'affectedLocationName',
            key: 'affectedLocationName',
            width: 150,
        },
     
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 150,
            render: (text, record) => (
                <Space size="middle">
                    {(record.village !== "") && record.village}
                    {record.commune !== "" && record.commune}
                    {record.district !== "" && record.district}
                    {record.province && record.province}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 150,
            render: (text, record) => (
                <Space size="middle">
                    {record?.case?.caseName}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'closeAt',
            key: 'closeAt',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                    {record.closeAt === null? "" : moment(record.closeAt).format("DD MM YYYY")}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'openAt',
            key: 'openAt',
            width: 100,
            render: (text, record) => (
                <Space size="middle">
                    {record.openAt === null? "" : moment(record.openAt).format("DD MM YYYY")}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 150,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 50,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    {/* <Link className="link" to={"/subSpecifylocation/" + record.id}><EditOutlined /></Link> */}
                    <span className="link" onClick={() => handleEdit(record)}><EditOutlined /></span>
                    <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}