import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subQuarantineCol = ({handleDelete, handleEditSubQuarantine,limit,page}) => {
    let no = (page-1) * limit
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <Space size="middle">
                    {no+=1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'quarantines',
            key: 'quarantines',
            width: 50,
            render: (text, record) => (
                <Space size="middle">
                    {record?.personalInfo?.lastName} {record?.personalInfo?.firstName}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 50,
            render: (text, record) => (
                <Space size="middle">
                    {moment(record?.date_in).format("DD MM YYYY")}
                </Space>
            ),
        },

        {
            title: '',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 50,
            render: (text, record) => (
                <Space size="middle">
                    {record?.date_out === null ? "":moment(record?.date_out).format("DD MM YYYY")}
                </Space>
            ),
        },

        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 90,
            render: (text, record) => (
                <Space size="middle">
                    {(record?.personalInfo?.village !== "") && record?.personalInfo?.village}
                    {record?.personalInfo?.commune !== "" && record?.personalInfo?.commune}
                    {record?.personalInfo?.district !== "" && record?.personalInfo?.district}
                    {record?.personalInfo?.province && record?.personalInfo?.province}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 30,
            render: (text, record) => (
                <Space size="middle">
                    {record?.personalInfo?.currentState?.confirm ? "" : ""}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'direct',
            key: 'direct',
            width: 30,
            render: (text, record) => (
                <Space size="middle">
                    {record?.personalInfo?.direct ? "" : ""}
                </Space>
            ),
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record?.personalInfo?.id}><EyeOutlined /></Link>
                    <span className="link" onClick={() => handleEditSubQuarantine(record)}><EditOutlined /></span>
                    <Popconfirm
                        title="?"
                        onConfirm={() => {handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]
    return array
}