import React, {useReducer, createContext} from 'react'
import { quarantineReducer } from '../reducer/quarantineReducer'

export const QuarantineController = createContext()

export default function QuarantineContext(props) {
    const [quarantineData, quarantineDataDispatch] = useReducer(quarantineReducer, [
        {
            id: "1",
            quarantineName: "",
            place: "",
            village: "" ,
            commune: "",
            district:'',
            province:'',
            inCharge:' ',
            tel:'015487762',
            note:'test',
            long:12.34343,
            lat:3433.34,
            capacity:30,
        },
        {
            id: "2",
            quarantineName: "",
            place: "",
            village: "" ,
            commune: "",
            district:'',
            province:'',
            inCharge:' ',
            tel:'087555999',
            note:'098554477',
            long:12.34343,
            lat:3433.34,
            capacity:30,
        },
    ])

    const [subQuarantineData, subQuarantineDataDispatch] = useReducer(quarantineReducer, [
        {
            id: "1",
            name: "",
            gender: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            status: "",
            relatedInfo:'',
        },
        {
            id: "2",
            name: "",
            gender: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            status: "",
            relatedInfo:'',
        },
    ])

    return (
        <QuarantineController.Provider
            value={{
                quarantineData,
                subQuarantineData,

                quarantineDataDispatch,
                subQuarantineDataDispatch,
            }}
        >
            {props.children}
        </QuarantineController.Provider>
    )
}
