import React, {useReducer, createContext} from 'react'
import { hospitalReducer } from '../reducer/hospitalReducer'

export const HospitalController = createContext()

export default function HospitalContext(props) {
    const [hospitalData, hospitalDataDispatch] = useReducer(hospitalReducer, [
        {
            id: "1",
            hospitalName: "",
            place: "",
            village: "" ,
            commune: "",
            district:'',
            province:'',
            inCharge:' ',
            tel:'015487762',
            note:'test',
            long:12.34343,
            lat:3433.34,
            capacity:30,
        },
        {
            id: "2",
            hospitalName: "",
            place: "",
            village: "" ,
            commune: "",
            district:'',
            province:'',
            inCharge:' ',
            tel:'087555999',
            note:'098554477',
            long:12.34343,
            lat:3433.34,
            capacity:30,
        },
    ])

    const [subHospitalData, subHospitalDataDispatch] = useReducer(hospitalReducer, [
        {
            id: "1",
            name: "",
            gender: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            status: "",
            relatedInfo:'',
        },
        {
            id: "2",
            name: "",
            gender: "",
            province: "",
            district: "",
            commune: "",
            village: "",
            status: "",
            relatedInfo:'',
        },
    ])

    return (
        <HospitalController.Provider
            value={{
                hospitalData,
                subHospitalData,

                hospitalDataDispatch,
                subHospitalDataDispatch,
            }}
        >
            {props.children}
        </HospitalController.Provider>
    )
}
